                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
                <v-layout row wrap>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Result1">效果</h3>
                                </v-card-title>
                                <v-divider></v-divider>
